main {
	margin: 0 auto;
	max-width: 90%;
  padding: 50px 30px;
}
:root {
  --primary: #6db784;
  --secondary: #1b4d89;
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);

}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: .1em;
  background: #6db784;
  background: var(--primary);
  color: #fafafa;
  color: var(--light);
  font-family: 'Helvetica Neue';
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin-bottom: 0px;
  margin-top: 70px;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}


button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: #1b4d89;
  background-color: var(--secondary);
}

button:hover {
  opacity: .8;
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid #000000;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid #1b4d89;
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

li {
  list-style: none;
}

nav a:hover {
  text-decoration: none;
}

nav  li {
  cursor: pointer;
}

textarea {
	width: 600px;
	height: 120px;
	border: 3px solid #cccccc;
	padding: 5px;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: calc(1rem * 0.5);
  margin-right: var(--spacing-one);
  margin-left: calc(1rem * 0.5);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: calc(1rem);
  margin-right: var(--spacing-two);
  margin-left: calc(1rem);
  margin-left: var(--spacing-two);
}
.ml-a{
  margin-left: auto;
}
.mr-a{
  margin-right: auto;
}
.my-1 {
  margin-top: calc(1rem * 0.5);
  margin-top: var(--spacing-one);
  margin-bottom: calc(1rem * 0.5);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: calc(1rem);
  margin-top: var(--spacing-two);
  margin-bottom: calc(1rem);
  margin-bottom: var(--spacing-two);
}

.my-5 {
  margin-top: calc(2.5rem);
  margin-top: var(--spacing-five);
  margin-bottom: calc(2.5rem);
  margin-bottom: var(--spacing-five);
}

.px-1 {
  padding-right: calc(1rem * 0.5);
  padding-right: var(--spacing-one);
  padding-left: calc(1rem * 0.5);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: calc(1rem);
  padding-right: var(--spacing-two);
  padding-left: calc(1rem);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: calc(1rem * 0.5);
  padding-top: var(--spacing-one);
  padding-bottom: calc(1rem * 0.5);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: calc(1rem);
  padding-top: var(--spacing-two);
  padding-bottom: calc(1rem);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.img-thumbnail {
  width: auto;
  height: 300px;
  margin-bottom: 50px;
  cursor: pointer;
}

.modalBackdrop {
	background: rgba(0, 0, 0, .65);
	bottom: 0;
	left: 0;
	overflow: auto;
	position: fixed;
	right: 0;
	top: 0;
	z-index: 1;
}

.modalContainer {
	background: #111111;
	background: var(--darker);
	border-radius: 5px;
	max-width: 100%;
	margin: 50px auto;
	padding: 15px;
	width: 960px;
}

.modalTitle {
	font-size: 18px;
	font-weight: 600;
	margin-bottom: 25px;
}

button {
	background: #6db784;
	background: var(--primary);
	border: 0;
	border-radius: 5px;
	color: #fff;
	cursor: pointer;
	font-size: 16px;
	font-weight: 600;
	margin-bottom: 30px;
	padding: 10px 15px;	
}

a {
	border-radius: 5px;
	color: #6db784;
	color: var(--primary);
	font-weight: 600;
	padding: 2px 4px;
	margin-left: -2px;
	margin-right: -2px;
	
}

.navActive {
  color: #1b4d89;
  color: var(--secondary);
}



h1::before{
  content: "<h1>";
  font-family: 'La Belle Aurore', cursive;
  color: #ffd700;
  font-size: 18px;
  position: absolute;
  margin-top: -20px;
  left: -40px;
  opacity: 0.6;
}

h1::after {
  content: '</h1>';
  font-family: 'La Belle Aurore', cursive;
  color: #ffd700;
  font-size: 18px;
  position: absolute;
  margin-top: 25px;
  margin-left: 20px;

  opacity: 0.6;
}

header{
    background: #f9e45b;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    height: 70px;
}

a{
    font-size: 22px;
    color: #fafafa;
    line-height: 51px;
    height: 51px;
    text-decoration: none;
    padding: 0 10px;
}

a:hover{
    color: #1b4d89;
}





footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
}

ul{
  float: right;
}
.push {
    flex-grow: 1
}

.page {
    width: 100%;
    height: 100%;
    position: absolute;
}

.top-tags {
    bottom: auto;
    top: 35px;
    left: 120px;
}

.top-tags-html {
    bottom: auto;
    top: 5px;
    left: 90px;
}

.tags {
    color: #ffd700;
    opacity: 0.6;
    position: absolute;
    bottom: 0;
    font-size: 18px;
    font-family: 'La Belle Aurore', cursive;
}

.bottom-tags {
    bottom: 35px;
    left: 120px;
}

.bottom-tag-html {
    margin-left: -20px;
    bottom: -50px;
}

.container {
    width: 100%;
    will-change: contents;
    height: 90%;
    min-height: 566px;
    position: absolute;
    opacity: 0;
    top: 5%;
    margin: 0 auto;
    z-index: 1;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-animation: fadeIn 1s forwards;
            animation: fadeIn 1s forwards;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}
.text-zone {
    position: absolute;
    font-family: 'La Belle Aurore', cursive;
    left: 30%;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 40%;
    max-height: 90%;
}


p{
    color: #1b4d89;
    margin-top: 20px;
    font-weight: 400;
    font-size: 11px;
    font-family: sans-serif;
    letter-spacing: 3px;
}

.flat-button {
    color: #ffd700;
    font-size: 13px;
    font-weight: 400;
    letter-spacing: 4px;
    font-family: sans-serif;
    text-decoration: none;
    border: 1px solid #ffd700;
    margin-top: 25px;
    float: right;
    -webkit-animation: fadeInAnimation 1s 1.8s backwards;
            animation: fadeInAnimation 1s 1.8s backwards;
    white-space: nowrap;
  }

.flat-button:hover{
    background: #ffd700;
    color: #1b4d89;
}


.grid-container{
    display: grid;
    grid-gap: 50px 100px;
    gap: 50px 100px;
    width: 90%;
    margin: 50px;
}
.grid-item1:hover,
.grid-item2:hover,
.grid-item3:hover,
.grid-item4:hover{
    border-radius: 10px;
    border: 3px solid rgba(27, 77, 137, 0.3);
   
}

.grid-item1 {
    grid-column-start: 1;
    grid-column-end: 1;
}
.grid-item2{
    grid-column-start: 2;
    grid-column-end: 2;
}

.grid-item3{
    grid-column-start: 3;
    grid-column-end: 3;
}

.grid-item4{
    grid-column-start: 3;
    grid-column-end: 3;
}

img{
    width: 100%;
    height: 200px;
    object-fit: cover;
}
.image-container{
    position: relative;
}


.image-effect{
    opacity: 1;
    display: block;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.image_overlay{
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 100px;
    left: 170px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.image-container:hover .image-effect{
    opacity: 0.5;
}

.image-container:hover .image_overlay{
    opacity: 1.5;
}

.image_title{
    font-family: 'Sacramento', cursive;
    color: #56BBF1;
    font-size: 50px;
    text-transform: lowercase;
}

.image-icon{
    width: 32px;
    height: 32px;
}
.res-grid-item1 {
    grid-column-start: 1;
    grid-column-end: 1;
    margin-left: 160px;
}

.res-grid-item2{
    grid-column-start: 2;
    grid-column-end: 2;
}

.grid-container{
    color: #1b4d89;
    font-family: sans-serif;
}
section {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    align-items: center;
}

label {
    display: flex;
}

input[type='text'],
input[type='email'] {
    width: 100%;
    border: 0;
    background: #115173;
    height: 50px;
    font-size: 16px;
    color: #fff;
    padding: 0 20px;
    box-sizing: border-box;
}

textarea {
    width: 100%;
    border: 0;
    background: #115173;
    height: 50px;
    font-size: 16px;
    color: #fff;
    padding: 20px;
    box-sizing: border-box;
    min-height: 150px;
}

.flat-button {
    color: #ffd700;
    font-size: 11px;
    letter-spacing: 3px;
    text-decoration: none;
    padding: 8px 10px;
    border: 1px solid #ffd700;
    float: left;
    border-radius: 4px;
    background: 0 0;
    text-transform: uppercase;
    float: right;
    text-align: center;
    margin-right: 10px;

}
